<template>
  <div id="record">
    <van-nav-bar fixed style="z-index:1001">
      <div slot="title">提现记录</div>
      <van-icon name="arrow-left" slot="left" color="black" @click="$router.go(-1)"/>
    </van-nav-bar>
	
    <van-list v-model="loading">
      <p style="margin-top: 2.6rem"></p>
      <div class="centerBox" v-for="(i,index) in details" :key="index">
        <div class="littleBox">
          <p>{{timestampToTime(i.created_at)}}</p>
        </div>
        <p v-if="i.status == -1"  style="background-color: #2d7fff;margin-top: 13px;border-radius: 0.3rem;color: #ffffff;padding: 0 0.3rem;">{{['提现失败','已拒绝','待审核','审核通过','提现成功'][parseInt(i.status) + parseInt(2)]}}</p>
        <p v-else-if="i.status == 2"  style="background-color: green;margin-top: 13px;border-radius: 0.3rem;color: #ffffff;padding: 0 0.3rem;">{{['提现失败','已拒绝','待审核','审核通过','提现成功'][parseInt(i.status) + parseInt(2)]}}</p>
        <p v-else-if="i.status == 0"  style="background-color: #A2DFFE;margin-top: 13px;border-radius: 0.3rem;color: #ffffff;padding: 0 0.3rem;">{{['提现失败','已拒绝','待审核','审核通过','提现成功'][parseInt(i.status) + parseInt(2)]}}</p>
        <p v-else-if="i.status == -2"  style="background-color: red;margin-top: 13px;border-radius: 0.3rem;color: #ffffff;padding: 0 0.3rem;">{{['提现失败','已拒绝','待审核','审核通过','提现成功'][parseInt(i.status) + parseInt(2)]}}</p>
        <p v-else-if="i.status == 1"  style="background-color: #ff9c67;margin-top: 13px;border-radius: 0.3rem;color: #ffffff;padding: 0 0.3rem;">{{['提现失败','已拒绝','待审核','审核通过','提现成功'][parseInt(i.status) + parseInt(2)]}}</p>
        <p class="money">-{{i.amount}}</p>
      </div>
    </van-list>
        
  </div>
</template>
<script>
  export default {
    name: "record",
    data() {
      return {
        loading: true,
        details:[],
        status:'',
      }
    },
    created() {
      this.detail()
    },
    methods: {
      detail(){
        let self = this
        this.$POST('/user/index').then(res =>{
          console.log(res);
          if(res.code===200 ){
            self.details = res.data
          }
        }).catch(res=>{
          this.$toast('连接服务器失败!');
          console.log(res);
        })
      },
      timestampToTime(timestamp) {
        var date = new Date(timestamp * 1000);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        var D = date.getDate() + ' ';
        var h = date.getHours() + ':';
        var m = date.getMinutes() + ':';
        var s = date.getSeconds();
        return Y + M + D + h + m + s;
      },
    },
  }
</script>

<style scoped>
  #container img {
    width: 100%;
    height: 100%;
  }
  .centerBox{
    display: flex;
    justify-content: space-between;
    padding: 0 1.2rem;
    border-bottom: 1px solid #f4f4f4;
  }
  .littleBox{
    display: flex;
    justify-content: space-around;
  }
  .littleBox :nth-child(2){
    margin-left: 0.8rem;
  }
  .money{
    color: #ff9c67;
  }
  .daBox{
    display: flex;
  }
</style>



